.home {
    display: flex;
    height: 100vh;
    justify-content: space-evenly;
    align-items: center;
    // margin-bottom: 4rem;
    top: 2vh;
    position: relative;
    gap: 12rem;
}

.home::before {
    content: "";
    height: 200px;
    width: 200px;
    position: absolute;
    top: 12%;
    left: 30%;
    background: #FFA600;
    opacity: 0.2;
    border-radius: 30% 70% 33% 67% / 47% 69% 31% 53%;

}

.home::after {
    content: "";
    height: 250px;
    width: 200px;
    position: absolute;
    top: 45%;
    left: -2rem;
    background: #43597d;
    opacity: 0.2;
    border-radius: 85% 15% 38% 62% / 49% 65% 35% 51%;
    z-index: -1;
}

.catch-phrase {
    h1 {
        font-size: 2rem;
    }

    p {
        font-size: 1rem;
    }

    span {
        color: #FFA600;
    }
}

.profil {
    position: relative;
}

.profil-img_wrapper {
    position: relative;
    width: 350px;

    &::before {
        content: "";
        position: absolute;
        width: 95%;
        height: 440px;
        background:
            #FFA600;
        opacity: .6;
        top: 44%;
        left: 43%;
        transform: translate(-50%, -50%);
        z-index: -20;
        border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
        background-image: -webkit-linear-gradient(90deg,
                transparent 0%,
                transparent 50%,
                white 50%,
                white 100%),
            -webkit-linear-gradient(0deg, rgba(0, 0, 0, 0.03) 0%, rgba(0, 0, 0, 0.03) 50%, white 50%, white 100%);
        background-size: 0.2em 0.2em;
    }
}

.developper,
.iAmFree {
    position: absolute;
    background: white;
    border-radius: 5px;
    box-shadow: 2.4px 2.4px 12.3px rgba(0, 0, 0, 0.05),
        19px 19px 61px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
    padding: 5px;
    display: flex;
    align-items: center;
    text-align: left;
}

.iAmFree {
    display: flex;
    top: 5%;
    left: -35%;
}

.developper {
    right: 0;
    bottom: 10%;
}

.profil-img_wrapper img {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    object-fit: contain;
    width: 100%;
}

.homeButtons {
    display: flex;
    justify-content: center;
    gap: 2rem;
}

.textWrapper>* {
    border: 0px;
    margin: 0px;
    padding: 0px;
}